.App {
  text-align: center;
}

.playback {
  height: 700px;
  width: 100%;
}

.DetailsBox {
  margin-top: 11%;
  height: 500px;
  width: 100%;
}

#boxIs {
  height: 380px;
}

.trailerBox {
  height: 400px;
  padding: 3% 6%;
}

.Head {
  text-align: start;
  margin-left: 5%;
  font-size: 28px;
  font-weight: 600;
}

.HeadRel {
  text-align: start;
  margin-left: 11%;
  font-size: 28px;
  font-weight: 600;
  margin-top: -3%;
}

.trailerBox > div > img {
  margin-top: 2%;
  height: 80%;
  width: 30%;
  border-radius: 12px;
}

.CastBox {
  height: 290px;
  display: flex;
  justify-content: space-between;
  padding: 3% 0%;
}

.CastBox > div {
  width: 15%;
  height: 95%;
  border-radius: 50%;
}

.CastBox > div > img {
  height: 100%;
  width: 100%;
  border-radius: 50%;
}

.blackDiv {
  padding: 4% 0%;
  background-color: rgb(0, 0, 0);
  color: rgb(167, 167, 163);
}
